import React from 'react'
import { VError } from 'verror'

import { useRoot } from '../../../atoms/RootProvider'
import {
  DeliveryTypeEnum,
  OrderStateCategoryEnum,
  OrderStateEnum,
} from '../../../graph/generated'
import { reportError } from '../../../helpers/sentry'
import { TOrderStateModel } from '../models/OrderStateModel'
import { OrderCancelButton } from '../molecules/OrderCancelButton'
import { OrderDeliveryToPickup } from '../molecules/OrderDeliveryToPickup'
import { OrderStateButton } from '../molecules/OrderStateButton'
import { OrderStateDeliver } from '../molecules/OrderStateDeliver'
import { OrderStateFlowContainer } from '../molecules/OrderStateFlowContainer'
import { OrderStateTimeNotification } from '../molecules/OrderStateTimeNotification'
import { QueryOrderState } from './OrderStateFlow.graphql'

interface IProps {
  orderId: ID
  branchId: ID
}

interface IStateProps extends IProps {
  stateModel: TOrderStateModel
  orderStateCategory: OrderStateCategoryEnum
  canCancel: boolean
  canDeliver: boolean
  isDelivery: boolean
}

const OrderStateNewOrder: React.FC<IStateProps> = props => (
  <OrderStateFlowContainer>
    {props.canCancel && <OrderCancelButton {...props} />}
    <OrderDeliveryToPickup {...props} />
    <OrderStateTimeNotification {...props} />
    <OrderStateButton nextState={OrderStateEnum.Accepted} {...props} />
  </OrderStateFlowContainer>
)

const OrderStateAcceptedOrder: React.FC<IStateProps> = props => (
  <OrderStateFlowContainer>
    {props.canCancel && <OrderCancelButton {...props} />}
    <OrderDeliveryToPickup {...props} />
    <OrderStateTimeNotification {...props} />
    {props.canDeliver ? (
      <OrderStateDeliver {...props} />
    ) : (
      <OrderStateButton nextState={OrderStateEnum.Finished} {...props} />
    )}
  </OrderStateFlowContainer>
)

const OrderStateReadyToDeliverOrder: React.FC<IStateProps> = props => (
  <OrderStateFlowContainer>
    {props.canCancel && <OrderCancelButton {...props} />}
    {props.isDelivery ? (
      <OrderStateDeliver {...props} />
    ) : (
      <OrderStateButton nextState={OrderStateEnum.Finished} {...props} />
    )}
  </OrderStateFlowContainer>
)

const OrderStateOnWayOrder: React.FC<IStateProps> = props => (
  <OrderStateFlowContainer>
    {props.canCancel && <OrderCancelButton {...props} />}
    <OrderStateTimeNotification {...props} />
    <OrderStateDeliver {...props} />
    <OrderStateButton nextState={OrderStateEnum.Finished} {...props} />
  </OrderStateFlowContainer>
)

const OrderStateFinishedOrder: React.FC<IStateProps> = props => (
  <OrderStateFlowContainer>
    {props.canCancel && <OrderCancelButton {...props} />}
  </OrderStateFlowContainer>
)

export const OrderStateFlow: React.FC<IProps> = ({ orderId, branchId }) => {
  const {
    order: { state: stateModel },
  } = useRoot()
  return (
    <QueryOrderState
      variables={{ orderId, branchId }}
      render={({ order, companyBranch: { allowedCategories } }) => {
        const { orderStateCategory } = order
        if (!orderStateCategory) {
          reportError(new VError('Order %s has got no state categories'))
          return null
        }
        const isDelivery =
          order.deliveryType !== null &&
          order.deliveryType.enum !== DeliveryTypeEnum.Pickup

        const canDeliver =
          isDelivery &&
          (allowedCategories.includes(OrderStateCategoryEnum.OnWay) ||
            allowedCategories.includes(OrderStateCategoryEnum.ReadyToDeliver))

        const canCancel = order.isCancelable

        const stateProps: IStateProps = {
          orderId,
          branchId,
          stateModel,
          orderStateCategory,
          canCancel,
          canDeliver,
          isDelivery,
        }
        switch (orderStateCategory) {
          case OrderStateCategoryEnum.NewOrders:
            return <OrderStateNewOrder {...stateProps} />
          case OrderStateCategoryEnum.Preparing:
            return <OrderStateAcceptedOrder {...stateProps} />
          case OrderStateCategoryEnum.ReadyToDeliver:
            return <OrderStateReadyToDeliverOrder {...stateProps} />
          case OrderStateCategoryEnum.OnWay:
            return <OrderStateOnWayOrder {...stateProps} />
          case OrderStateCategoryEnum.Finished:
            return <OrderStateFinishedOrder {...stateProps} />
          default:
            return null
        }
      }}
    />
  )
}
