import { Trans } from '@lingui/macro'
import React from 'react'

import { CardDock } from '../../../atoms/CardDock'
import { Sizer } from '../../../atoms/Sizer'
import { MCancelOrder } from '../../../graph/types'
import { useGuardedMutation } from '../../../hooks/useGuardedMutation'
import { ConfirmButton } from '../../../molecules/ConfirmButton'
import { CancelOrderMutation } from '../../createorder/models/CreateOrderModel.graphql'

interface IProps {
  orderId: ID
}

export const OrderCancelButton: React.FC<IProps> = ({ orderId }) => {
  const cancelOrder = useGuardedMutation<
    MCancelOrder.Mutation,
    MCancelOrder.Variables
  >(CancelOrderMutation)

  return (
    <CardDock position="BottomLeft">
      <Sizer width={7.375}>
        <ConfirmButton
          confirmNode={<Trans>Really cancel?</Trans>}
          onConfirm={() => {
            cancelOrder({ orderId })
          }}
        >
          <Trans>Cancel order</Trans>
        </ConfirmButton>
      </Sizer>
    </CardDock>
  )
}
