import gql from 'graphql-tag'

import { OrderTickerFragment } from '../../../segments/order/molecules/OrderTicker.graphql'
import { OrderStateCategoryFragment } from '../../order/models/OrderStateModel.graphql'

export const OrdersStatisticsListQuery = gql`
  query GOrdersStatisticsList(
    $branches: [ID!]!
    $dateTimeFrom: DateTime!
    $dateTimeTo: DateTime!
    $pagination: InputPaginationParams!
  ) {
    stats: statistics(
      branches: $branches
      dateTimeFrom: $dateTimeFrom
      dateTimeTo: $dateTimeTo
    ) {
      orders: finishedOrders {
        list: orders(includeCanceled: true, pagination: $pagination) {
          ...GOrderListTicker
          ...GOrderStateCategory
        }
      }
    }
  }

  ${OrderTickerFragment}
  ${OrderStateCategoryFragment}
`
