import { isoDate } from '@speedlo/graphql'
import { DocumentNode } from 'graphql'
import { createContext } from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import {
  IQueryResult,
  useExecutableQuery,
} from '../../../hooks/useExecutableQuery'

interface IVariables {
  readonly dateTimeFrom: string
  readonly dateTimeTo: string
  readonly branches: ReadonlyArray<string>
}

const getInitialResult = () => ({
  data: null,
  loading: false,
  hasAnyError: false,
  wasExecuted: false,
})

export function createStatisticsQuery<TData, TVariables extends IVariables>(
  query: DocumentNode,
) {
  const context = createContext<IQueryResult<TData>>(getInitialResult())
  const useStatisticsQuery = () => {
    const { statistics } = useRoot()
    const { result, execute, reset } = useExecutableQuery<TData, IVariables>(
      query,
    )
    const executeFilter = () => {
      reset()
      execute({
        branches: statistics.branchesFilter,
        dateTimeFrom: isoDate(statistics.dateTimeFrom),
        dateTimeTo: isoDate(statistics.dateTimeTo),
      })
    }
    const defaultValues = {
      dateTimeFrom: statistics.dateTimeFrom,
      dateTimeTo: statistics.dateTimeTo,
    }
    return { result, executeFilter, resetResult: reset, defaultValues }
  }
  return {
    context,
    useStatisticsQuery,
  }
}
