import React from 'react'
import styled from 'styled-components'

import { GOrderDetail } from '../../../graph/types'
import { i18n } from '../../../i18n'
import { OrderWarePriceLine } from './OrderWarePriceLine'

interface IProps {
  order: GOrderDetail.Order
}

const OrderCartTotalsContainer = styled.div`
  margin: 0 0 0 auto;
  width: 20rem;
  text-align: right;
`

export const OrderDetailTotals: React.FC<IProps> = ({ order }) => (
  <OrderCartTotalsContainer>
    <OrderWarePriceLine
      label={i18n.t`Transport fee`}
      price={order.transportFee.value}
    />
    <OrderWarePriceLine
      label={i18n.t`Covers price`}
      price={order.coverPrice.value}
    />
    <OrderWarePriceLine
      label={i18n.t`Discount total`}
      price={order.totalDiscount.value * -1}
    />
    <OrderWarePriceLine
      label={i18n.t`Total price`}
      price={order.totalSum.value}
      highlight={true}
    />
  </OrderCartTotalsContainer>
)
