import { Trans } from '@lingui/macro'
import { Timelapse } from '@material-ui/icons'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { GOrderDetail } from '../../../graph/types'
import { formatTimeWithoutTodayDate } from '../../../helpers/formats'
import { OrderTimeLabel } from '../atoms/OrderTimeLabel'

interface IProps {
  order: GOrderDetail.Order
}

export const OrderStats: React.FC<IProps> = ({ order }) => {
  const stats: ReactNode[] = []
  if (order.deliverAt) {
    stats.push(
      <div key="deliverAt">
        {formatTimeWithoutTodayDate(order.deliverAt)}
        <OrderTimeLabel>
          <Trans>Deliver on time</Trans>
        </OrderTimeLabel>
      </div>,
    )
  }
  if (order.driver) {
    const { firstName, lastName } = order.driver.user
    stats.push(
      <div key="driver">
        {`${firstName} ${lastName}`}
        <OrderTimeLabel>
          <Trans>Driver</Trans>
        </OrderTimeLabel>
      </div>,
    )
  }
  if (stats.length === 0) {
    return null
  }
  return (
    <IconLine icon={Timelapse} labelMargin="0">
      {stats}
    </IconLine>
  )
}
