import React from 'react'
import { useMutation } from 'react-apollo-hooks'

import { MChangeOrderToPickup } from '../../../graph/types'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { ChangeOrderToPickupMutation } from '../models/OrderStateModel.graphql'

interface IProps {
  orderId: ID
  isDelivery: boolean
}

export const OrderDeliveryToPickup: React.FC<IProps> = ({
  orderId,
  isDelivery,
}) => {
  const changeToPickup = useMutation<
    MChangeOrderToPickup.Mutation,
    MChangeOrderToPickup.Variables
  >(ChangeOrderToPickupMutation, { variables: { orderId } })
  if (!isDelivery) {
    return null
  }
  return (
    <TouchButton
      text={i18n.t`Change to pickup`}
      onClick={() => {
        changeToPickup()
      }}
    />
  )
}
