import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useMemo, useRef } from 'react'
import { useApolloClient } from 'react-apollo-hooks'
import {
  AutoSizer,
  CellMeasurer,
  CellMeasurerCache,
  IndexRange,
  InfiniteLoader,
  List,
} from 'react-virtualized'

import { CardListTitle } from '../../../atoms/CardListTitle'
import { useRoot } from '../../../atoms/RootProvider'
import { GOrdersStatisticsList } from '../../../graph/types'
import { formatDateISO } from '../../../helpers/formats'
import { OrderBox } from '../../../segments/order/atoms/OrderBox'
import { OrdersStatisticsListQuery } from './OrdersStatisticsList.graphql'
import { useOrderStatistics } from './OrdersStatisticsLoader'

export const OrdersStatisticsList = observer(() => {
  const { statistics, order: orderModel } = useRoot()
  const { data, wasExecuted } = useOrderStatistics()
  const loaderRef = useRef<InfiniteLoader | null>(null)

  const orders = useRef(new Map())

  const measureCache = useMemo(
    () =>
      new CellMeasurerCache({
        fixedWidth: true,
        defaultHeight: 55,
        minHeight: 55,
      }),
    [],
  )

  useEffect(
    () => {
      // whenever this turns to false it means that filter was executed again
      // and everything should reset in here
      if (wasExecuted === false) {
        orders.current.clear()
        measureCache.clearAll()
        if (loaderRef.current) {
          loaderRef.current.resetLoadMoreRowsCache(true)
        }
      }
    },
    [wasExecuted],
  )

  const client = useApolloClient()

  const queryMoreOrders = async ({ startIndex, stopIndex }: IndexRange) => {
    const result = await client.query<
      GOrdersStatisticsList.Query,
      GOrdersStatisticsList.Variables
    >({
      query: OrdersStatisticsListQuery,
      variables: {
        branches: statistics.branchesFilter,
        dateTimeFrom: formatDateISO(statistics.dateTimeFrom),
        dateTimeTo: formatDateISO(statistics.dateTimeTo),
        pagination: { fromIndex: startIndex, toIndex: stopIndex },
      },
    })

    const list = [...result.data.stats.orders.list]
    for (let index = startIndex; index <= stopIndex; index++) {
      orders.current.set(index, list.shift())
    }
  }

  let totalCount = 0
  if (data && data.stats) {
    const { finishedCount, cancelledCount } = data.stats.finishedOrders
    totalCount = finishedCount + cancelledCount
  }

  return (
    <AutoSizer>
      {({ width, height }) => (
        <InfiniteLoader
          rowCount={totalCount}
          isRowLoaded={({ index }) => orders.current.has(index)}
          loadMoreRows={queryMoreOrders}
          ref={loaderRef}
        >
          {({ onRowsRendered, registerChild }) => (
            <List
              ref={registerChild}
              onRowsRendered={onRowsRendered}
              rowHeight={measureCache.rowHeight}
              deferredMeasurementCache={measureCache}
              rowCount={totalCount}
              width={width}
              height={height}
              noRowsRenderer={() => (
                <CardListTitle>
                  {wasExecuted ? <Trans>No orders</Trans> : null}
                </CardListTitle>
              )}
              rowRenderer={({ index, key, style, parent }) => {
                const order = orders.current.get(index)
                if (!order) {
                  return (
                    <div style={style} key={key}>
                      ...
                    </div>
                  )
                }
                return (
                  <CellMeasurer
                    key={key}
                    columnIndex={0}
                    rowIndex={index}
                    cache={measureCache}
                    parent={parent}
                  >
                    {({ measure }) => (
                      <div style={style}>
                        <OrderBox
                          order={order}
                          toggleOrderDetail={o => {
                            orderModel.list.toggleOrderDetail(o)
                            setTimeout(measure, 500)
                          }}
                          isOrderDetailOpen={orderModel.list.isOrderDetailOpen}
                          showBranch={statistics.branchesFilter.length > 1}
                        />
                      </div>
                    )}
                  </CellMeasurer>
                )
              }}
            />
          )}
        </InfiniteLoader>
      )}
    </AutoSizer>
  )
})
