import gql from 'graphql-tag'

import { CustomerFragment } from '../../../graph/fragments'
import { FAddressFragmentDoc } from '../../../graph/generated'

export const OrderTickerFragment = gql`
  fragment GOrderListTicker on Order {
    id
    createdAt
    isPaidWithCard
    deliveryType {
      enum
    }
    deliverAt
    deliverAtWorst
    deliveredAt
    companyBranch {
      id
      name
      oldOrderThreshold
    }
    address {
      ...FAddress
    }
    customer {
      ...GCustomer
    }
    totalSum {
      value
      currency {
        code
      }
    }
  }
  ${FAddressFragmentDoc}
  ${CustomerFragment}
`
